@import url(https://fonts.googleapis.com/css2?family=Libre+Caslon+Display&display=swap);
:root {
  --color-blue: #75aadb;
  --color-white: #ffffff;
  --color-gold: #fcbf49;
  --color-kobe: #d17e73;
  --color-bg: #22272cec;
  --font-primary: 'Libre Coslon Display', serif;
  
}

body {
  margin: 0;
  padding: 0;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  font-family: 'Libre Coslon Display', serif;
  font-family: var(--font-primary);
}

.root {
  background-size: contain;
  background-blend-mode: multiply;
  background-color: #22272cec;
  background-color: var(--color-bg);
  height: 100vh;
  width: 100vw;
  overflow: hidden;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  position: relative;
}

.inputs {
  width: 300px;
  display: flex;
  justify-content: space-between;
}

.input {
  width: 70px;
  height: 60px;
  font-size: 2em;
  text-align: center;
  border-radius: 30%;
  background: #22272cec;
  background: var(--color-bg);
  color: #fcbf49;
  color: var(--color-gold);
  border: 1px solid;
}

.inputs__group {
  display: flex;
  flex-direction: column;
  height: 110px;
  justify-content: space-between;
  align-items: center;
}

.inputs__min-group input {
  color: #d17e73;
  color: var(--color-kobe);
  border-color: #d17e73;
  border-color: var(--color-kobe);
}

.inputs__min-group label {
  color: #d17e73;
  color: var(--color-kobe);
}
.inputs__max-group label {
  color: #75aadb;
  color: var(--color-blue);
}
.inputs__max-group input {
  color: #75aadb;
  color: var(--color-blue);
  border-color: #75aadb;
  border-color: var(--color-blue);
}

.inputs__group label {
  font-size: 25px;
}

.button {
  border-radius: 50%;
  background: #75aadb;
  background: var(--color-blue);
  outline: 5px #75aadb;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 15%;
}

.number {
  color: #fcbf49;
  color: var(--color-gold);
  font-size: 10em;
  position: absolute;
  transform: translate(0, -100%);
}
.svg {
  height: 300px;
  display: block;
  margin: 0 auto;
}

.path {
  animation-duration: 1s;
  animation-name: pulse;
  animation-iteration-count: infinite;
}

.path-7 {
  animation-delay: -1s;
}

.path-6 {
  animation-delay: -0.875s;
}
.path-5 {
  animation-delay: -0.75s;
}
.path-4 {
  animation-delay: -0.625s;
}
.path-3 {
  animation-delay: -0.5s;
}
.path-2 {
  animation-delay: -0.375s;
}
.path-1 {
  animation-delay: -0.25s;
}
.path-0 {
  animation-delay: -0.125s;
}

@keyframes pulse {
  0% {
    opacity: 0.1;
  }
  30% {
    opacity: 0.8;
  }
  100% {
    opacity: 0.1;
  }
}

